 body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.table-container {
  overflow-x: auto;
}

.user-table {
  width: 100%;
  border-collapse: collapse;
}

.user-table td {
  padding: 3px;
  border: 1px solid #ddd;
  overflow-x: auto;
  height: 45px;
}

.user-table th {
  background-color: #f2f2f2;
  overflow-x: auto;
  text-align: left;
  height: 70px;
}


.user-table td button {
  margin-right: 1px;
  text-align: center;
  height: 30px;
}

@media (max-width: 600px) {
  .user-table {
    font-size:25px;
  }
}

.container {
  display: flex;
  margin: 1em;
}

.crud {
  flex: 3;
}

.logger {
  margin-left: 0em;
  padding-left: 4px;
  flex: 0.9em;
  background: rgba(215, 228, 236, 0.537);
  border: 1px solid rgb(230, 246, 171);
  min-height: 100px;
  ul {
    padding: 10px;
    list-style: none;
  }
  li {
    margin-bottom: 3px;
    color: rgb(18, 206, 18);
    &.error {
      color: rgb(128, 11, 11);
    }
  }
}

tr.edited {
  background-color: rgb(13, 174, 10);
}

.user-table:first {
  margin-bottom: 2em;
}
